import { defineDocumentType, makeSource } from 'contentlayer/source-files';
import rehypeSlug from 'rehype-slug';
import rehypeAutolinkHeadings from 'rehype-autolink-headings';
import remarkGfm from 'remark-gfm';

export const Post = defineDocumentType(() => ({
  name: 'Post',
  filePathPattern: `posts/**/*.md`,
  contentType: 'mdx',
  fields: {
    title: { type: 'string', required: true },
    slug: { type: 'string', required: false },
    tags: { type: 'list', of: { type: 'string' }, required: false },
    published_at: { type: 'date', required: false },
    description: { type: 'string', required: false }
  },
  computedFields: {
    url: {
      type: 'string',
      resolve: (doc) =>
        `/blog/${doc.slug || doc._raw.flattenedPath.replace('posts/', '')}`
    },
    flattenedPath: {
      type: 'string',
      resolve: (doc) => doc._raw.flattenedPath.replace('posts/', '')
    }
  }
}));

export const Page = defineDocumentType(() => ({
  name: 'Page',
  filePathPattern: `pages/**/*.md`,
  contentType: 'mdx',
  fields: {
    title: { type: 'string', required: true },
    slug: { type: 'string', required: false },
    description: { type: 'string', required: false }
  },
  computedFields: {
    url: {
      type: 'string',
      resolve: (doc) =>
        `/pages/${doc.slug || doc._raw.flattenedPath.replace('pages/', '')}`
    },
    flattenedPath: {
      type: 'string',
      resolve: (doc) => doc._raw.flattenedPath.replace('pages/', '')
    }
  }
}));

export default makeSource({
  // Use the existing blog repo as content source
  contentDirPath: '../Blog 文章原稿',
  documentTypes: [Post, Page],
  contentDirExclude: ['Arc 瀏覽器使用心得.md'],
  fieldOptions: {
    // Ignore frontmatter `type: post|page` and rely on filePathPattern
    typeFieldName: 'docType'
  },
  mdx: {
    remarkPlugins: [remarkGfm],
    rehypePlugins: [
      rehypeSlug,
      [rehypeAutolinkHeadings, { behavior: 'wrap' }]
    ],
    esbuildOptions: (options) => {
      options.external = options.external ?? [];
      options.external.push('react/jsx-dev-runtime');
      return options;
    }
  }
});
